import axios from 'axios'

export const servicepageModule = {
    state: () => ({
        service: [],
        page1: {},
        page2: {},
        page3: {},
        currentPage: {},
        status: '',
    }),
    mutations: {
        setServices(state, services) {
            state.service = services
        },
        setPage1(state, page) {
            state.page1 = page
        },
        setPage2(state, page) {
            state.page2 = page
        },
        setPage3(state, page) {
            state.page3 = page
        },
        setCurrentPage(state, page) {
            state.currentPage = page
        },
        setStatus(state, status) {
            state.status = status
        }
    },
    actions: {
        async getting({commit, state}) {
            try {
                console.log(2)
                const response = await axios.get('https://6385dd59875ca3273d483eb0.mockapi.io/services');
                commit('setServices', response.data)
                // state.services.forEach((e, i) => {
                //     if (i == 0) {
                //         commit('setPage1', e)
                //     } else if (i == 1) {
                //         commit('setPage2, e')
                //     } else (
                //         commit('setPage3', e)
                //     )
                // })
                if (state.status == 'tel') {
                    commit('setCurrentPage', state.service[0])
                } else if (state.status == 'tab') {
                    commit('setCurrentPage', state.service[1])
                } else {
                    commit('setCurrentPage', state.service[2])
                }

            } catch (e) {
                console.log(e)
            }   
        }
    },
    namespaced: true
}