import { createRouter, createWebHistory } from 'vue-router'
import HomeView from "@/views/HomeView.vue"
import Tel from '@/views/ServiceTelView.vue'
import Tab from '@/views/ServiceTabView.vue'
import Note from '@/views/ServiceNoteView.vue'
import Contacts from '@/views/ContactsView.vue'
import PageNotFound from '@/views/PageNotFound.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/service-tel',
    name: 'service-tel',
    component: Tel
  },
  {
    path: '/service-tab',
    name: 'service-tab',
    component: Tab
  },
  {
    path: '/service-note',
    name: 'service-note',
    component: Note
  },
  {
    path: '/contacts',
    name: 'contacts',
    component: Contacts
  },
  { 
    path: '/:pathMatch(.*)*', 
    component: PageNotFound 
  }

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
