<template>
 <div class="wrapper">
    <nav>
        <ul class="nav">
            <li @click="$router.push('/')" class="nav--link">Главная</li>
            <li @click="$router.push('/service-tel')" class="nav--link">Ремонт телефонов</li>
            <li @click="$router.push('/service-tab')" class="nav--link">Ремонт планшетов</li>
            <li @click="$router.push('/service-note')" class="nav--link">Ремонт ноутбуков</li>
            <li @click="$router.push('/contacts')" class="nav--link">Контакты</li>
        </ul>
    </nav>
 </div>
</template>

<script>
import {mapActions, mapGetters, mapMutations, mapState} from 'vuex'
export default {
    methods: {
        ...mapMutations({
            setStatus: 'services/setStatus'
        }),
        serviceTel() {
            this.setStatus(this.status, 'tel')
        },
        serviceTab() {
            this.setStatus(this.status, 'tab')
        },
        serviceNote() {
            this.setStatus(this.status, 'note')
        }
    },
    computed: {
        ...mapState({
            status: state => state.services.status
        })
    }
}
</script>

<style lang="scss" scoped>
.nav {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.nav--link {
    text-decoration: none;
    list-style-type: none;
    color: white;
    cursor: pointer;
}

.nav--link:hover {
    text-decoration: underline;
    text-decoration-color: white;
}
</style>