<template>
  <section class="FQ">
    <div class="FQ__container wrapper">
      <div class="FQ__accordeon">
        <h2>Часто задаваемые вопросы</h2>
        <ul class="accordeon-block" v-for="question in questions" :key="question.id">
          <li>
            <questions-card @active="questionActive" :card="question"/>
          </li>
        </ul>
      </div>
      <img src="../assets/iphone-repair.png" alt="">
    </div>
  </section>
</template>

<script>
import {mapActions, mapGetters, mapMutations, mapState} from 'vuex'
import questionsCard from '@/components/Questions-card.vue'
export default {
  components:{
    questionsCard,
  },
  methods: {
    ...mapActions({
      fetchQuestions: 'home/fetchQuestions'
    }),
    ...mapMutations({
      setQuestions: 'home/setQuestions'
    }),
    questionActive(card) {
      if (card.status) {
        return card.status = !card.status
      } else {
        return card.status = !card.status
      }

    }
  },
  computed: {
    ...mapState({
      questions: state => state.home.questions
    })
  },
  mounted() {
    this.fetchQuestions()
  }
}
</script>



<style lang="scss" scoped>
.FQ__container {
  margin: 60px auto;
  display: flex;
  justify-content: space-between;

  h2 {
    margin-bottom: 20px;
    font-size: 30px;
  }
  
  img{ 
    width: calc(50% - 30px);
    height: 596px;
  }
}

.FQ__accordeon {
  width: 100%;
}

.accordeon-block {
  li {
    list-style-type: none;
    display: flex;
    padding: 20px 20px 20px 10px;
    border-top: 1px solid black;
    box-shadow: 0 7px 6px -6px #000;
    justify-content: space-between;
    flex-direction: column;
    max-width: 704px;
    width: 100%
  }
}
</style>