<template>
    <div class="accordeon-block__header" @click="questionActive">
        <h3>{{card.title}}?</h3>
        <img src="../assets/arrow.svg" alt="">
    </div>
    <p v-if="card.status">{{card.text}}</p>
</template>

<script>
export default {
    data() {
        return {
            cardActivated: true
        }
    },
    props: {
        card: {
            required: true
        }
    },
    methods: {
        questionActive() {
            this.$emit('active', this.card)
        }
    }
}
</script>

<style lang="scss" scoped>
.accordeon-block__header {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;

    img {
      height: 20px;
      width: 20px;
      transform: rotate(90deg);
      margin-left: 20px;
    }
}
</style>